import React from "react";
import {Route, Switch} from "react-router-dom";

import asyncComponent from "util/asyncComponent";

const App = ({match}) => (
  <div className="gx-main-content-wrapper">
    <Switch>
      <Route path={`${match.url}patients`} component={asyncComponent(() => import('./Doctor/Patients'))}/>
      <Route path={`${match.url}profile`} component={asyncComponent(() => import('./Doctor/Patients/Profile'))}/>
      <Route path={`${match.url}dashboard`} component={asyncComponent(() => import('./Doctor/Dashboard'))}/>
      <Route path={`${match.url}graphs/products`} component={asyncComponent(() => import('./Doctor/Graphs/Products'))}/>
      <Route path={`${match.url}graphs/pathologies`} component={asyncComponent(() => import('./Doctor/Graphs/Pathologies'))}/>
      <Route path={`${match.url}graphs/combined`} component={asyncComponent(() => import('./Doctor/Graphs/Combined'))}/>
      <Route path={`${match.url}consultations`} component={asyncComponent(() => import('./Doctor/Consultation'))}/>

      <Route path={`${match.url}doctors`} component={asyncComponent(() => import('./Master/Doctors'))}/>
      <Route path={`${match.url}doctorid`} component={asyncComponent(() => import('./Master/Doctors/Profile'))}/>
      <Route path={`${match.url}statistics`} component={asyncComponent(() => import('./Master/Statistics'))}/>
      <Route path={`${match.url}m/dashboard`} component={asyncComponent(() => import('./Master/Dashboard'))}/>
      <Route path={`${match.url}m/graphs/products`} component={asyncComponent(() => import('./Master/Graphs/Products'))}/>
      <Route path={`${match.url}m/graphs/pathologies`} component={asyncComponent(() => import('./Master/Graphs/Pathologies'))}/>
      <Route path={`${match.url}m/graphs/combined`} component={asyncComponent(() => import('./Master/Graphs/Combined'))}/>
      <Route path={`${match.url}m/graphs/doctors`} component={asyncComponent(() => import('./Master/Graphs/Doctors'))}/>
      <Route path={`${match.url}m/consultations`} component={asyncComponent(() => import('./Master/Consultation'))}/>
    </Switch>
  </div>
);

export default App;
